
import { defineComponent } from 'vue';
import { MatomoDialog } from 'CoreHome';

interface TestVueState {
  count: number;
  showDialog: boolean;
}

export default defineComponent({
  props: {
    contentTitle: String,
  },
  components: {
    MatomoDialog,
  },
  data(): TestVueState {
    return {
      count: 12,
      showDialog: false,
    };
  },
  methods: {
    increment() {
      this.count += 1;
      this.showDialog = this.count > 15;
    },
    decrement() {
      this.count -= 1;
      this.showDialog = this.count > 15;
    },
  },
});
